html{height:100%}html #app{color:#2c3e50;width:80%;margin:10px auto;font-family:Source Sans Pro, Helvetica, sans-serif;text-align:center}html #app .file-server{z-index:1}html #app .file-server span{height:30px;line-height:30px;display:block;float:left}html #app .file-server .server-address{float:left}html #app .file-server .server-qrcode{float:right}html #app .file-server .server-qrcode span:last-child{float:right;width:30px;height:30px;line-height:30px;background-image:url("./assets/shrink.png");background-size:27px 27px;text-align:center}html #app .popup{width:300px;height:300px;z-index:99;background:rgba(0,0,0,0.2);position:absolute;left:50%;top:50%;margin-left:-150px;margin-top:-150px}html #app .popup img{margin-top:50px}html #app .file-list{margin-top:30px;height:45px;line-height:45px}html #app .file-list .list-top{background:rgba(0,0,0,0.3);border:0.5px solid #ccc;border-radius:3px;color:#fff;text-align:left;padding-left:10px}html #app .file-list .list-body .file-specifc{height:40px;line-height:40px;border-bottom:0.5px solid rgba(44,62,80,0.5)}html #app .file-list .list-body .file-specifc span{display:block;line-height:40px;width:25%;height:40px;float:left}html #app .file-list .list-body .file-specifc span:first-child{padding-left:5px;width:48%;text-align:left}html #app .file-list .list-body .file-specifc .download{background:url("./assets/download.png");background-size:30px 30px;background-repeat:no-repeat;cursor:pointer;opacity:0.6;background-position:center}html #app .file-list .list-body .file-specifc .download:hover{opacity:1}html #app .file-list .list-body .file-specifc .delete{background:url("./assets/delete.png");background-size:30px 30px;background-repeat:no-repeat;cursor:pointer;opacity:0.6;background-position:center}html #app .file-list .list-body .file-specifc .delete:hover{opacity:1}html .clear{clear:both;width:0px;height:0px}



